@import "../../../styles/variables";

.main {
	width: 100%;
	max-height: 100svh;
	display: grid;
	grid-template-columns: 50px 1fr 50px;
	grid-template-rows: 1fr 20px;
	gap: 5px;
}
.pagination-dots {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 10px;
	grid-column: 2;
	grid-row: 2;
	&_dot {
		width: 10px;
		height: 10px;
		border-radius: 50%;
		background-color: $TERTIARY_COLOR;
		transition: 0.2s ease-in;
		&.active-dot {
			background-color: #00ffeb;
		}
	}
}
.weather {
	&_container {
		overflow: hidden;
	}
	&_forecast {
		height: 100%;
		display: flex;
	}
}
@media (width<=$MOBILE_WIDTH) {
	.main {
		grid-template-columns: none;
	}
	.pagination-dots {
		grid-column: auto;
	}
}
